<template>
  <div class="personal">
    <Card>
      <div slot="title">个人中心</div>
      <div slot="content">
        <div>
          <span>管理员ID:{{ personal.id }}</span>
          <el-divider></el-divider>
          <span>账号：{{ personal.account }}</span>
          <el-divider></el-divider>
          <span>用户组：{{ personal.userGroup }}</span>
          <el-divider></el-divider>
          <span>创建时间：{{ personal.ctime }}</span>
          <el-divider></el-divider>
          <div class="img">
            <span>管理头像：</span>
            <el-upload
              class="avatar-uploader"
              action="http://127.0.0.1:5000/users/avatar_upload"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img v-if="imgUrl" :src="baseUrl + imgUrl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>
          <el-button type="primary" @click="imgBtn">修改头像</el-button>
        </div>
      </div>
    </Card>
  </div>
</template>

<script>
import Card from "@/components/Card";
//引入api
import { api_getInfo ,api_updataImg} from "@/api/acc.js";
//引入时间更改模块
import moment from "moment";
export default {
  components: {
    Card,
  },
  data() {
    return {
      baseUrl: "http://127.0.0.1:5000/upload/imgs/acc_img/",
      imgUrl: "",
      personal: {},
    };
  },
  methods: {
    //成功上传图片
    handleAvatarSuccess(val) {
        //成功上传之后返回到页面上
      this.imgUrl = val.imgUrl
    },
    beforeAvatarUpload() {},
    // 定义渲染函数
    async getUserInfo() {
      let { accountInfo } = await api_getInfo();
      accountInfo.ctime = moment(accountInfo.ctime).format("YYYY-MM-DD HH:mm:ss");
      this.personal = accountInfo;
    },
    async imgBtn(){
        let {code} =await api_updataImg({
            imgUrl:this.imgUrl
        })
        if(code==0){
            this.$bus.$emit("update");
        }
    }
  },
  created() {
    this.getUserInfo();
  },
};
</script>

<style lang="less" scoped>
.personal {
  .img {
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    /deep/ .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409eff;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 120px;
      height: 120px;
      line-height: 120px;
      text-align: center;
    }
    .avatar {
      width: 120px;
      height: 120px;
      display: block;
    }
  }
}
</style>